#game {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.choice {
  width: 100px;
  height: 100px;
  cursor: pointer;
  color: #fff;
  background-color: #000;
  border: 2px solid #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  font-size: 2em;
  display: flex;
}

.choice:active {
  transform: scale(.95);
}

#restart, #result {
  margin: 5px;
}

h1 {
  color: gold;
  display: flex;
}

